export class Navbar extends React.Component {
    render() {
      return (
        <div className="navbar-base">
          <div className="navbar-internal-div">

            <div onClick={this.props.sideNavAnimation} style={{ display: "inline-block" }}><HamburgerMenu /></div>
            <Link to="/"><HomeIcon /></Link>
            <h1 className="navbar-text">{this.props["pageName"]}</h1>

            <Badges />
            
          </div>
        </div>
      );
    }
  }

export class TeacherNavbar extends React.Component {
  render() {
    return (
      <div className="navbar-base">
          <div className="navbar-internal-div">

            <Link to="/teacher"><HomeIcon /></Link>
            <h1 className="navbar-text">Teacher</h1>            
          </div>
        </div>
    )
  }
}


export class SideNav extends React.Component {
  constructor(props) {
    super(props)
    this.state = {teamMembers: []}
    this.updateTeamMembers()
  }
  async updateTeamMembers() {
    var teamMembers = await getTeamMembers()
    this.setState({teamMembers: teamMembers})
  }
    render() {
      var sideNavIconSize = "25";
      return (
        <div className="sidenav" style={isMobile ? {width: "60vw", maxWidth: "60vw"} : null}>
          <div className="sidenav-internal-div">
            <Link to="/journey">
            <div>
            <CompassIcon size={sideNavIconSize} />
            <p1 className="sidenav-text">Your Journey</p1>
            </div>
            </Link>
            <Link to="/portfolio" >
            <div >
              <PortfolioIcon size={sideNavIconSize} />
              <p1 className="sidenav-text">Your Portfolio</p1>
            </div>
            </Link>
            <Link to="/workspace">
              <div>
                <WorkspaceIcon size={sideNavIconSize} />
                <p1 className="sidenav-text">Your Workspace</p1>
              </div></Link>
              <Link to="/perksandrewards">
              <div style={{marginTop: "40px"}}>
                <PerksAndRewardsIcon size={sideNavIconSize} />
                <p1 className="sidenav-text">Perks and Rewards</p1>
              </div></Link>
              {(this.props.showTeams ? <div style={{marginTop: "10vh"}}>
              <h1 style={{fontSize: "25px"}}>Team</h1>
              <div style={{marginLeft: "15px"}}>
              {this.state.teamMembers.map((item, index) => <div style={{alignItems: "center", display: "flex", marginTop: "15px"}}><ProfileIcon size={"35px"} 
              teamOrder={index}/> {item}</div>)}
              
              </div>
              </div> : null)}

          </div>

        </div>
     )
    }
  }
