function makeToDoList(lst, offset = 0) {
    return (lst.map((content, index) => {
        content = content.split("//>")
        return ({
        id: `${index} ${offset}`,
        content: content[0],
        assignedUser: JSON.parse(content[1]),
        difficulty: content[2],
        hours: content[3],
        dueDate: content[4],
        actionSteps: JSON.parse(content[5])
        //assignedUser: "John" //Doesnt work because of setState not updatig in ToDo
    })}
    ))
}
