export async function integratePortfolioFolders(fileMap, type) {
    var folders = await getPortfolioFolders();
    folders.forEach((item) => {
        if (item.folder_id === "qwerty123456") {
        return
    }
        fileMap.fileMap[item.folder_id] = {
        "django_id": item.id,
        "id":item.folder_id,
        "name":item.folder_name,
        "isDir":true,
        "modDate":"2020-10-24T17:48:39.866Z",
        "childrenIds": (item.files? item.files: []),
        "childrenCount":(item.files? item.files.length: 0),
        "parentId": item.parent_id}
        if (item.parent_id === "qwerty123456"){
            
            fileMap.fileMap["qwerty123456"].childrenIds.push(item.folder_id)
            fileMap.fileMap["qwerty123456"].childrenCount ++;
        }
        
    })
    var files = await getPortfolioFiles();
    files.forEach((item) => {
        fileMap.fileMap[item.file_id] = {
        "django_id": item.file_id,
        "id":item.file_id,
        "name":item.filename,
        "modDate":"2020-10-24T17:48:39.866Z",
        "parentId": item.parent_id,
        "download": item.file}
        if (item.parent_id === "qwerty123456"){
            fileMap.fileMap["qwerty123456"].childrenIds.push(item.folder_id)
            fileMap.fileMap["qwerty123456"].childrenCount ++;
        }
        
    })
    console.log(fileMap)
    return fileMap;  
}

export async function getRequestToDoList() {
    
    var todo_list;
    await axios.get(`https://cambio-api.herokuapp.com/api/Groups/${localStorage.getItem("group")}/`, {
        headers: {
        'Authorization': `token ${localStorage.getItem("token")}`
        }
        }).then(
        response => {
            console.log(response)
            todo_list = [makeToDoList(response.data.backlog),
            makeToDoList(response.data.to_do, 1000), makeToDoList(response.data.doing, 2000),
           makeToDoList(response.data.done,3000)];
            
        }
    )
    return todo_list

}
