export var username;
export var getUserInfo = async () => {
    var userData;
    await axios.get('https://cambio-api.herokuapp.com/api/Students/', {
            headers: {
            'Authorization': `token ${localStorage.getItem("token")}`
            }
            })
            .then((response) => {
            console.log(response)
            localStorage.setItem("group", response.data[0].group_name)
            user.cash = response.data[0].cash
            user.xp = response.data[0].xp
            userData = response.data[0]
            username = response.data[0].name
            })
            .catch((error) => {
            console.error(error)
            logout()
            window.location.reload()
            })
            return userData
        }
    


