var checkpoint;
var studentsList;
export async function getClassInfo() {
    if (studentsList) {
        return studentsList
    }
    var students = [];
    await axios.get("https://cambio-api.herokuapp.com/api/School/", {
        headers: {
        'Authorization': `token ${localStorage.getItem("token")}`
        }
        }).then(
        res => {
            students = res.data[0].students
            checkpoint = res.data[0].checkpoint
        }
    ).catch(res => {
        if (res.message === "Request failed with status code 403") {
            return []
        }
    }
        )
    return students
}


