class userData extends React.Component {
  constructor(props) {
    super(props)
    this.state = {thing: "", thing2: ""}
    this.updateTest()
    
  }
  updateTest = async () => {
await axios.get('https://cambio-api.herokuapp.com/api/Students/', {
        headers: {
        'Authorization': `token ${localStorage.getItem("token")}`
        }
        })
        .then((response) => {
        console.log(response.data)
        this.setState({thing: JSON.stringify(response.data)})
        })
        .catch((error) => {
        console.error(error)
        })
        await axios.get('https://cambio-api.herokuapp.com/api/School/', {
            headers: {
            'Authorization': `token ${localStorage.getItem("token")}`
            }
            }).then(response => {
              this.setState({thing2: JSON.stringify(response.data)})
            })

  }

  render() {
    const queryClient = new QueryClient()
    return (<>
    <QueryClientProvider client={queryClient}>
       <Example />
     </QueryClientProvider>
    <div>{(localStorage.getItem("user"))} {this.state.thing}</div>
    <div>{this.state.thing2}</div>
    </>)
  }
}
function Example() {
  const { data } = useQuery('repoData', () =>
    fetch(`https://cambio-api.herokuapp.com/api/Groups/${localStorage.getItem("group")}/`).then(res =>
      res.json()
    )
  )



  return (
    <div>
      {JSON.stringify(data)}
    </div>
  )
}
var updatePage1 = () => {}

export var updatePage = () => {
  updatePage1()
}

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {updateKey: false, students: [], assignments: []}
    this.updateStudents()
    updatePage1 = () => {
      this.setState({updateKey: !this.state.updateKey})
    }
    
  }
  updateStudents = async () => {
    var students = await getClassInfo()
    var assignments = await getAssignmentNames()
    this.setState({students: students, assignments: assignments})
  }
  render() {
  getRequestToDoList()
  return (
    <div key={this.state.updateKey}>
    <HashRouter basename={process.env.PUBLIC_URL}>
      <Switch>
      <LoginRoute path="/login" exact component={LoginPage} />
      <LoginRoute path="/signup" exact component={SignupPage} />
      <PrivateRoute path="/" exact component={HomePage} />
      <PrivateRoute path="/userData" exact component={userData} />
      <PrivateRoute path="/workspace" exact component={WorkspacePage} />
      <PrivateRoute path="/journey" exact component={JourneyPage} />
      <PrivateRoute path="/perksandrewards" exact component={PerksAndRewardsPage} />
      <PrivateRoute path="/portfolio" exact component={PortfolioPage} />
      <PrivateRoute path="/teacher" exact component={TeacherPage} />
      <PrivateRoute path="/teacher/class" exact component={TeacherClassPage} />
      <PrivateRoute path="/teacher/class/students" exact component={TeacherOverviewStudentPage} />
      <PrivateRoute path="/teacher/class/assignments" exact component={TeacherOverviewAssignmentPage} />
      {[this.state.assignments.map(assignment => <PrivateRoute path={`/teacher/class/${assignment}`} exact component={() => <TeacherOverviewStudentPage link={`/teacher/class/${assignment}/`}/>}/>),
      this.state.students.map(student => <>
      <PrivateRoute path={`/teacher/class/${student}`} exact component={() => <TeacherSingleStudentOverviewPage studentName={student}/>} />
      {this.state.assignments.map(assignment => <PrivateRoute path={`/teacher/class/${assignment}/${student}`} exact component={() => <TeacherSingleAssignmentPage assignmentName={assignment}/>} />)}</>
    )]}
      
     
      
      </Switch>
    </HashRouter>
    </div>
  );
}}

export default App;
