import React from "react";
import { TeacherNavbar } from '../reused-components/navigation.js';
import {getTurnedInMasterAssignments} from "../backend.js"
import { ProfileIcon, DownloadIcon } from "../reused-components/icons.js";

export class TeacherSingleAssignmentPage extends React.Component {
  constructor(props) {
    super(props)
    this.studentOrTeacher = "t"
    this.state = {comments: [], currentComment: ""}
  }
  loadMasterAssignment = () => {
    
  }
  addComment = (e) => {
    if (e !== 13 || this.state.comment === "") {
      return
    }
    var comments = this.state.comments;
    comments.push(this.studentOrTeacher + this.state.comment)
    this.setState({comments: comments})
  }
  changeCurrentComment = (comment) => {
    this.setState({comment: comment})
  }
    render(){
      return (
        <div>
          <TeacherNavbar/>
          <div style={{paddingTop: "3vh", paddingLeft: "9vw", position: "relative"}}>
            <h1 style={{display: "inline-block"}}>
                <AssignmentIcon /> {this.props.assignmentName} </h1>
                <div style={{height: "50vh", width: "45vw", position: "absolute", display: "flex", justifyContent: "flex-end", flexDirection: "column", overflowY: "auto"}}>
                  {this.state.comments.map(comment => <div style={{marginTop: "5%"}}>{((comment[0] === this.studentOrTeacher)?
                  <div style={{backgroundColor: "#e8e8e8", width: "45%", borderRadius: "10px", height: "auto"}}><div style={{padding: "5%"}}>{comment.slice(1)}</div></div> :
                  <div style={{backgroundColor: "#e8e8e8", width: "45%", marginLeft: "55%", borderRadius: "10px"}}><div style={{padding: "5%"}}>{comment.slice(1)}</div></div>)}</div>)}
            <div style={{width: "45vw", height: "10vh", backgroundColor: "#F8F8F8", bottom: "7vh", position: "fixed", display: "flex", alignContent: "center", borderRadius: "2vh"}}>
            <ProfileIcon /> <div style={{marginTop: "-10px"}}><input style={{width: "35vw"}} placeholder="Write comment here...." onChange={(comment) => this.changeCurrentComment(comment.target.value)}
            onKeyDown={(e) => this.addComment(e.keyCode)}
            /></div>
            </div>
            </div>
          <div style={{display: "inline-block"}}><SingleAssignmentCompleteOverview assignmentName={this.props.assignmentName} /></div>
        </div>
        </div>
      )
    }
}


class AssignmentIcon extends React.Component {
  render() {
    return (<svg id="assignment_black_24dp" xmlns="http://www.w3.org/2000/svg" width="40px" height="40px" viewBox="0 0 99.678 99.678">
    <path id="Path_86" data-name="Path 86" d="M0,0H99.678V99.678H0Z" fill="none"/>
    <path id="Path_87" data-name="Path 87" d="M69.452,9.306H52.091a12.41,12.41,0,0,0-23.424,0H11.306A8.331,8.331,0,0,0,3,17.613V75.758a8.331,8.331,0,0,0,8.306,8.306H69.452a8.331,8.331,0,0,0,8.306-8.306V17.613A8.331,8.331,0,0,0,69.452,9.306Zm-29.073,0a4.153,4.153,0,1,1-4.153,4.153A4.165,4.165,0,0,1,40.379,9.306Zm8.306,58.145H19.613V59.145H48.686Zm12.46-16.613H19.613V42.532H61.145Zm0-16.613H19.613V25.919H61.145Z" transform="translate(9.46 3.153)"/>
  </svg>
  )
  }
}




class SingleAssignmentCompleteOverview extends React.Component {
  render() {
    return (
      <div style={{ position: "absolute", right: "9vw", 
      height: "calc(100vh - 105px - 5vh)", width: "30vw", borderLeft: "2px solid grey", paddingLeft: "2vw", marginTop: "-40px"}}>
      <div style={{position: "relative", height: "100%", width: "100%"}}>
      <h1>View Work</h1>
      <div style={{width: "85%", height: "10%", backgroundColor: "#F8F8F8", borderRadius: "2vh", alignItems: "center", display: "flex", position: "relative"
    , cursor: "pointer"
    }}>
        <div style={{padding: "3%", alignItems: "center", display: "flex"}} onClick={getTurnedInMasterAssignments}>
        <AssignmentIcon/>{this.props.assignmentName} 
        
        </div>
        <div style={{position: "absolute", right: "3%"}}>
          <DownloadIcon />
        </div>
      </div>
      <div style={{width: "85%", height: "40%", backgroundColor: "#F8F8F8", marginTop: "5%", borderRadius: "2vh"}}>
        <div style={{padding: "3%"}}>
      <div style={{fontWeight: "bold"}}>Points</div>
      <div><input style={{height: "15px", width: "15px"}} type="checkbox"
                          defaultChecked={true} />50 Points: Write Assignment </div>
                          <div><input style={{height: "15px", width: "15px"}} type="checkbox"
                          defaultChecked={true} />50 Points: Write Assignment </div>
                          </div>

      </div>
      <div style={{position: "absolute", bottom: "15%"}}>Grade</div>
      <div style={{position: "absolute", right: "calc(15%)", bottom: "15%"}}>Turned in 11/13</div>
      </div>
      </div>
    )
  }
}



