import React from 'react';
import { Route, Redirect } from 'react-router-dom';
import {isLogin,  getClassInfo} from '../backend';
import Loader from "react-loader-spinner";
import logo from "../logo.png";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";

class LoadingScreen extends React.Component {
    constructor(props) {
        super(props)
        this.state= {isLogingIn: true}
        this.checkLogin()
    }
    async checkLogin() {
        if (await getClassInfo()) {
            this.setState({isLogingIn: false})
        }
    }
    render() {
        if (this.state.isLogingIn){
        return (
        <div style={{height: "100vh", width: "100vw", display: "flex", justifyContent: "center", alignItems: "center"}}>
            <div style={{width:"30vh"}}>
            <img src={logo} style={{height: "30vh"}} alt="Cambio logo"/>
           <div style={{display: "flex", justifyContent: "center"}}> <Loader type="ThreeDots" color="#00BFFF" height={80} width={"10vw"} /></div>
            </div>
        </div>)}
        return this.props.component
    }
}
export const PrivateRoute = ({component: Component, ...rest}) => {
    return (

        // Show the component only when the user is logged in
        // Otherwise, redirect the user to /signin page
        
        <Route {...rest}  render={props => (
            isLogin() ?
            <LoadingScreen component={<Component {...props} />}/>
            : <Redirect to="/login" />
        )} />
        
    );
};

//Redirects from loginpage to homepage if logged in
export const LoginRoute = ({component: Component, ...rest}) => {
    return (
        <Route {...rest} render={props => (
            !isLogin() ?
                <Component {...props} />
            : <Redirect to="/" />
        )} />
    );
};
/*
export default PrivateRoute;
export default LoginRoute; */